.header {
	&__box {
		height: 60px;
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media @mobile-xl {

		}
		@media @mobile-land {

		}
		@media @tablet {

		}
		@media @desktop {

		}
		@media @desktop-xl {

		}
		@media @desktop-xl-2 {
			height: 86px;
		}
	}
	&__logo {
		flex-basis: 120px;
		max-width: 120px;
		flex: 1;

		@media @mobile-xl {

		}
		@media @mobile-land {

		}
		@media @tablet {
			flex-basis: 140px;
			max-width: 140px;
		}
		@media @desktop {
			flex: inherit;
		}
		@media @desktop-xl {
			flex-basis: 170px;
		}
	}
	.nav {
		order: 5;
		@media @desktop {
			flex: 1;
			order: inherit;
		}
	}
	&__list {
		display: flex;
		flex-direction: column;
		align-items: center;
		@media @desktop {
			margin-top: inherit;
			flex-direction: row;
			justify-content: flex-start;
		}
	}
	&__item {
		width: 100%;
		text-align: center;
		@media @desktop {
			margin-left: 20px;
			width: inherit;
			text-align: inherit;
		}
		@media @desktop-xl {
			margin-left: 35px;
		}
	}
	&__link {
		display: inline-block;
		position: relative;
		font-size: 20px;
		line-height: 60px;
		width: 100%;
		transition: all .3s linear;

		@media @desktop {
			line-height: @lh;
			font-size: @fs;
			width: inherit;
			transition: all 1s cubic-bezier(0.215, 0.61, 0.355, 1) 1.5s;
		}

		&:after {
			@media @desktop {
				content: '';
				position: absolute;
				top: 55%;
				left: 0;
				transform: translateY(-50%);
				width: 100%;
				height: 1px;
				background-color: #000;
				transition: transform .3s  linear;		
				transform: scaleX(0);
				transform-origin: bottom right; 
				z-index: 10;
			}
		}
		&:hover {
			transition: all .5s;
			&:after{
				transform-origin: bottom left;
				transform: scaleX(1);
			}
		}
	}
	.localization {
		display: flex;
		&__item {
			margin-left: 10px;
			@media @desktop {
				margin-left: 5px;
			}
			@media @desktop-xl {
				margin-left: 10px;
			}
		}
	}
	&__phone {
		display: none;
		@media @tablet {
			padding: 0 15px;
			display: flex;
			align-items: center;
		}
	}
	li {
		list-style: none;
	}
	.btn {
		display: none;
		@media @desktop {
			display: block;
			overflow: hidden; 
		}
	}
}

.header__fixed {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	transform: translateZ(0);
	background-color: #eee;
	will-change: transform;
	transition: transform .5s cubic-bezier(.72,-0.04,.14,1.03);
	z-index: 200;
	@media @desktop {
		background-color: transparent;
	}
}
.header__fixed.is-hidden {
	transform: translateY(-120%);
}

// BONUS MOBILE NAV STYLE
.socialMob {
	&__list {
		display: flex;
		justify-content: space-between;
		align-items: center;
		background: #e6e6e6;
		box-shadow: 0px 2px 8.71px 4.29px rgba(0, 0, 0, 0.14);
		@media @desktop {
			display: none;
		}
	}
	&__link {
		display: block;
		margin-bottom: 30px;
		transition: all .3s linear;

		&:hover {
			.fb__circle {
				fill: #3b5998;
			}
			.bh {
				fill: #1769ff;
			}
			.skype {
				fill: #00aaf2;
			}
			.pin {
				fill: #cb1f24;
			}
		}
	}
	&__img {
		width: 50px;
		max-height: 50px;
		transition: all .3s linear;
		fill: #474747;
	}
}

.header__item-mob {
	padding: 0 15px;
	display: flex;
	align-items: center;
	@media @desktop {
		display: none;
	}
}




.hero {
	position: relative;
	overflow: hidden;
	min-height: 640px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background: url(../images/hero__back.jpg)no-repeat center/cover;
	text-align: center;

	@media @mobile-xl {

	}
	@media @mobile-land {

	}
	@media @tablet {
		padding: 0;
		text-align: inherit;
	}
	@media @desktop {
		min-height: 660px;
	}
	@media @desktop-xl-2 {
		min-height: 890px;
	}

	&__title {
		padding-bottom: 25px;
		font-size: 19px;
		font-weight: 700;
		font-family: @ff2;
		text-transform: uppercase;

		@media @mobile-xl {
			font-size: 21.5px;
		}
		@media @mobile-land {

		}
		@media @tablet {
			font-size: 36.7px;
		}
		@media @desktop {
			font-size: 42px;
		}
		@media @desktop-xl-2 {
			padding-bottom: 35px;
			font-size: 50px;
		}

		&-accent {
			display: block;
			font-size: 23px;

			@media @mobile-xl {
				font-size: 26px;
			}
			@media @mobile-land {

			}
			@media @tablet {
				font-size: 44px;
			}
			@media @desktop {
				font-size: 50.5px;
			}
			@media @desktop-xl-2 {
				font-size: 60px;
			}
		}

	}
	&__subtitle {
		padding-bottom: 10px;
		font-size: 19px;


		@media @mobile-xl {

		}
		@media @mobile-land {

		}
		@media @tablet {
			font-size: 21px;
			max-width: 470px;
		}
		@media @desktop {
			max-width: 650px;
		}
		@media @desktop-xl-2 {
			padding-bottom: 20px;
			font-size: 22px;
		}
	}

	.btn {
		margin: 20px auto;
		display: table;
		position: relative;
		width: 250px;
		font-size: 16px;
		z-index: 1;
		transition: all .3s linear;

		@media @tablet {
			margin: inherit;
			padding: 15px 0;
			width: 275px;
			font-size: 18px;
		}

		&:before {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-30%);
			right: 15px;
			width: 18px;
			height: 14px;
			background: url(../images/icons/cup.png)no-repeat;
			@media @tablet {
				right: 30px;
			}
		}
	}
}

.container {
	position: relative;
}

.decoration {
	&__img {
		display: none;
		@media @desktop-xl {
			display: block;
			position: absolute;
			transition: transform .4s linear;
			&:hover {
				transform: scale(1.09)!important;
			}
		}
	}
	&__imgBig-medium {
		display: none;
		@media @tablet {
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%) rotate(-34deg);
			right: 0;
			max-width: 380px;
		}
		@media @desktop {
			max-width: 500px;
			top: 58%;
			transform: translateY(-50%) rotate(-15deg);
		}
		@media @desktop-xl {
			display: none;
		}
	}

